<template>
  <div class="nav">
    <el-row>
      <el-col :xs="9" :sm="9" :md="9" :lg="9" :xl="9">
        <a class="navbar" href=""><img class="logo" src="../assets/img/logo.png" alt="程序人生资产管理"></a>
      </el-col>
      <el-col :xs="15" :sm="15" :md="15" :lg="15" :xl="15" style="padding: 20px 0;" class="big">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" text-color="#354656" router
                 active-text-color="#5ca2df">
          <el-menu-item index="/">首页</el-menu-item>
          <el-submenu index="2">
            <template slot="title">关于我们</template>
            <el-menu-item index="/about">公司简介</el-menu-item>
            <el-menu-item index="2-2">公司结构</el-menu-item>
            <el-menu-item index="2-3">大事记</el-menu-item>
          </el-submenu>
          <el-menu-item @click="production">旗下产品</el-menu-item>
          <el-submenu index="4">
            <template slot="title">最新资讯</template>
            <el-menu-item index="/history_list">赛事热点</el-menu-item>
            <el-menu-item index="4-2">媒体报道</el-menu-item>
          </el-submenu>
          <el-menu-item index="5">人才招聘</el-menu-item>
        </el-menu>
      </el-col>
      <el-col :xs="15" :sm="15" :md="15" :lg="15" :xl="15" class="btn_div">
        <a @click="show">
          <el-button class="btn"><i class="iconfont icon-caidanmenu45"></i></el-button>
        </a>
      </el-col>
    </el-row>
    <!--小屏幕-->
    <el-collapse-transition>
      <div class="transition-box" v-if="onshow">
        <el-row>
          <el-col :span="24" class="small">
            <el-menu
              default-active="1"
              class="el-menu-vertical-demo"
              text-color="#354656" active-text-color="#5ca2df"
            >
              <el-menu-item index="/">
                首页
              </el-menu-item>
              <el-submenu index="2">
                <template slot="title">关于我们</template>
                <el-menu-item-group>
                  <el-menu-item index="/about">公司简介</el-menu-item>
                  <el-menu-item index="2-2">公司架构</el-menu-item>
                  <el-menu-item index="2-3">大事记</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-menu-item index="3">
                <span slot="title" @click="production">旗下产品</span>
              </el-menu-item>
              <el-submenu index="4">
                <template slot="title">最新资讯</template>
                <el-menu-item-group>
                  <el-menu-item index="/history_list">赛事热点 </el-menu-item>
                  <el-menu-item index="4-2">媒体报道</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-menu-item index="5">
                <span slot="title">人才招聘</span>
              </el-menu-item>
            </el-menu>
          </el-col>

        </el-row>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script>
  import ElRow from "element-ui/packages/row/src/row";

  export default {
    components: {ElRow},
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1',
        onshow: false,
      }
    },
    mounted(){
      this.activeIndex = this.$route.path;
    },
    methods: {
      show() {
        this.onshow = !this.onshow;
      },
      production(){
        window.location.href='http://www.programlifefund.com/Home/Product/index'
      }
    }
  }
</script>

<style scoped>
  .navbar {
    font-family: 'Droid Sans';
    font-size: 30px;
    font-weight: 400;
    height: auto;
    line-height: 40px;
    margin-right: 50px;
    padding: 24px 0px;
    float: left;
    padding-left: 10%;
  }

  .logo {
    width: 138px;
    height: 49px;
    vertical-align: middle;
    display: block;
    border: 0;
  }

  .el-menu-demo {
    border: 0;
    font-weight: bold;
    float: right;
    padding-right: 10%;
  }

  .el-menu-vertical-demo {
    border: 0;
    font-weight: bold;
  }

  .el-menu-demo li {
    border: 0 !important;
    font-size: 15px !important;
  }

  .el-menu-item {
    margin-right: 15px;
  }

  .nav {
    background-color: #ffffff;
    border: 0px none;
    position: relative;
    min-height: 60px;
    padding: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    border-radius: 0px;
    border-bottom: 7px solid #5ca2df;
    box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.04);
  }

  .containter {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }

  .small {

    border-top: 1px solid #e7e7e7;
  }

  .btn_div {
    display: none;
  }
  .block{
    display: block;
    color: rgb(53, 70, 86)
  }
  .btn_div a {
    font-family: 'Droid Sans';
    font-size: 30px;
    font-weight: 400;
    height: auto;
    line-height: 40px;
    margin-right: 50px;
    padding: 24px 0px;
    float: right;
    padding-right: 10%;
  }

  .btn {
    padding: 0 10px;
  }

  .btn .iconfont {
    font-size: 29px;
  }

  @media screen and (min-width: 1200px) {
    .containter {
      width: 1170px;
    }

    .small {
      display: none;
    }
  }

  @media screen and (min-width: 992px) {
    .containter {
      width: 970px;
    }
  }

  @media screen and (min-width: 768px) {
    .containter {
      width: 750px;
    }
  }

  @media screen and (min-width: 1200px) {

  }

  @media (max-width: 990px) and (min-width: 768px) {
    .btn_div {
      display: block;
    }

    .big {
      display: none;
    }

    .el-menu-item {
      margin-right: 0;
    }
  }

  @media only screen and (max-width: 768px) and (min-width: 480px) {
    .btn_div {
      display: block;
    }

    .big {
      display: none;
    }

    .el-menu-item {
      margin-right: 0;
    }
  }

</style>



