<template>
    <div>
      <heads></heads>
      <page_head :name="name"></page_head>
      <div class="container">
        <el-row>
          <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16" class="slide">
            <div class="page-content">
              <p>{{intro}}</p>
              <p>
                <span v-for="item in context">{{item.type}}：{{item.name}}<br/></span>
              </p>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="slide">
            <page_right></page_right>
          </el-col>
        </el-row>
      </div>
    </div>
</template>

<script>
 import heads from './../template/header'
 import page_head from './../template/page-header'
 import ElRow from "element-ui/packages/row/src/row";
 import page_right from './../template/page-right'
  export default{
   components:{
     ElRow,
     heads,page_head,page_right
   },
    data(){
     return{
        name:'公司简介',
       intro:"",
       context:''
     }
    },
    mounted(){
      this.context = this.$store.state.company.detail;
      this.intro = this.$store.state.company.introduction;
    }
  }
</script>

<style scoped>
  .slide{
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  }
  .page-content{
    margin: 0;
    padding: 60px 0 60px;
  }
  .page-content p{
    margin-bottom: 20px;
    font-family: 'Droid Sans';
    color: #737f85;
    font-size: 15px;
    font-weight: 400;
    line-height: 25px;
    word-wrap: break-word;
  }
</style>



