<template>
  <div class="sidebar">
    <div class="search widget">
      <el-input clearable placeholder="请输入搜索内容" style="margin-bottom: 20px;"></el-input>
      <el-button type="primary" style="padding: 7px 10px">Search</el-button>
    </div>
    <div class="widget">
      <h3>近期文章</h3>
      <ul>
        <li v-for="item in context">{{item.title}}</li>
      </ul>
    </div>
    <div class="widget">
      <h3>文章归档</h3>
      <ul>
        <li v-for="item in context">{{item.title}}</li>
      </ul>
    </div>
    <div class="widget">
      <h3>分类目录</h3>
      <ul>
        <li><router-link to="/history_list">赛事热点</router-link></li>
        <li>媒体报道</li>
      </ul>
    </div>
  </div>
</template>

<script>
  import contexts from './../template/context'
export default {
  components:{
    contexts
    },
  data(){
    return{
      context:''
    }
  },
  mounted(){
    this.context = this.$store.state.index.match;
  }
}
</script>

<style scoped>
  .sidebar{
    background: linear-gradient(to right, rgba(248,248,248,1) 0%, rgba(255,255,255,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f8f8f8', endColorstr='#ffffff', GradientType=1 );
    border-left: 1px solid #e4e8ea;
    overflow: hidden;
    padding: 60px 0 60px 0px;
    margin: 0px;
  }
  .slide{
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  }
  .widget{
    margin: 0 0 40px;
    padding: 0 0 40px 25px;
    display: block;
    border-bottom: 4px double #e4e8ea;
  }
  .widget h3{
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    display: block;
    margin: 0 0 22px;
  }
  .widget li{
    border-bottom: 1px dotted #b4bfc5;
    padding: 9px 0;
    color: #737f85;
    font-size: 15px;
    line-height: 24px;
    margin: 0;
    vertical-align: top;
    cursor: pointer;
  }
  .widget a{
    color: #737f85;
  }
  .widget a:hover{
    color: #2a7bc1;
  }
</style>



