<template>
  <div class="page-header">
    <div class="container">
      <el-row>
        <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
          <div class="page-title"><h2>{{name}}</h2><p></p></div>
        </el-col>
        <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
          <ul class="page-ul">
            <li><router-link to="/">Home</router-link>  &nbsp;/ &nbsp; </li>
            <li>{{name}}</li>
          </ul>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
    export default {
        props:['name']
    }
</script>

<style scoped>
.page-header{
  background-color: #fefeff;
  background-image: linear-gradient(to bottom, #fefeff, #f4f6f7);
  background-repeat: repeat-x;
  border-bottom: 1px solid #e4e8ea;
  border-top: 1px solid #e4e8ea;
  padding: 18px;
}
  .page-title h2{
    margin: 0;
    font-weight: 400;
    word-wrap: break-word;
    font-size: 30px;
    line-height: 35px;
  }
  .page-ul{
    list-style: none outside none;
    margin-bottom: 0px;
    padding: 17px 0;
    font-family: 'Droid Sans';
    font-weight: 500;
    text-align: right;
    font-size: 16px;
    line-height: 20px;
    margin: 0 0 10px;
  }
  .page-ul li{
    display: inline-block;
    padding: 0;
    color: #737f85;
    font-size: 15px;
    line-height: 24px;
    margin: 0;
    vertical-align: top;
  }
  a{
    color: #337ab7;
  }
</style>



