import Vue from 'vue'
import Router from 'vue-router'
import main from '@/components/index'
import none from '@/components/404'
import history from '@/components/history_list'
import about from '@/components/aboutUs'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: main,
      meta:{
        title:"程序人生资产管理"
      }
    },
    {
      path:'/history_list',
      component:history,
      meta:{
        title:"赛事历史"
      }
    },
    {
      path:'/about',
      component:about,
      meta:{
        title:"关于我们"
      }
    },
    {
      path:'*',
      component:none,
      meta: {
        title: '404'
      }
    }
  ]
})





