<template>
    <div class="main">
        <img src="./../assets/img/logo.png" alt="">
        <el-row>
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" class="tit">您的请求已爆炸....请返回...</el-col>
        </el-row>
        <el-button @click="goback" type="primary">返回首页</el-button>
    </div>
</template>

<script>
    export default {
       data(){
         return{

         }
       },
      methods:{
         goback(){
           this.$router.push('/')
         }
      }
    }
</script>

<style scoped>
.main{
  width: 100%;
  text-align: center;
}
  .main img{
    margin: 0 auto;
    margin-top: 10%;
  }
  .tit{
    text-align: center;
    font-size: 25px;
    padding: 20px;
  }
</style>



