<template>
    <div>
      <a class="goup" @click="slideup" v-if="onshow">
        <i class="iconfont icon-xiangshang2"></i>
      </a>
    </div>
</template>

<script>
    export default {
        data(){
          return{
            onshow:false,
            scroll:''
          }
        },
      mounted(){
          window.addEventListener('scroll',this.slide)
      },
      methods:{
        slideup(){
          let go = function () {
            let position = document.documentElement.scrollTop || document.body.scrollTop;
            position -= 30;
            if(position > 0){
              window.scrollTo(0,position)
            }else{
              window.scrollTo(0,0);
              clearInterval(timer);
              timer = nul;
            }
          }
          let timer = setInterval(go,1)
        },
        slide(){
         this.scroll = document.documentElement.scrollTop || document.body.scrollTop;
         if(this.scroll > 500){
          this.onshow = true
         }else{
           this.onshow = false
         }
        }
      }
    }
</script>

<style scoped>
.goup{
  background-color: #5b6f7f;
  border-radius: 10%;
  bottom: 25px;
  height: 30px;
  overflow: hidden;
  padding: 5px;
  position: fixed;
  right: 25px;
  text-align: center;
  width: 30px;
  z-index: 9999;
  cursor: pointer;
  display: block;
}
  .goup i{
    color: #ffffff;
    font-size: 23px;
    line-height: 1.4;
  }
</style>



