<template>
    <div>
      <heads></heads>
      <pageTitle :name="name"></pageTitle>
      <div class="container">
        <el-row>
          <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16" class="slide">
            <div class="site-content">
              <template v-for="item in context">
                <div class="post">
                  <span class="author">
                    <img src="./../assets/img/author.png" alt="">
                  </span>
                  <contexts :data="item"></contexts>
                </div>
              </template>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="slide">
            <page_right></page_right>
          </el-col>
        </el-row>
      </div>
      <slid></slid>
    </div>
</template>

<script>
  import heads from './../template/header'
  import pageTitle from './../template/page-header'
  import contexts from './../template/context'
  import ElRow from "element-ui/packages/row/src/row";
  import slid from './../template/slideup'
  import page_right from './../template/page-right'
  export default {
    components:{
      ElRow,
      heads,pageTitle,contexts,slid,page_right
    },
    data(){
      return{
        context:'',
        name:"赛事热点"
      }
    },
    mounted(){
      this.context = this.$store.state.index.match;
    }
  }
</script>

<style scoped>
.site-content{
  margin: 0;
  padding: 60px 0 60px;
  border-left: 3px solid #e4e8ea;
}
  .site-content .post{
    margin: 0 0 40px;
    padding: 0 0 40px 30px;
    border-bottom: 1px solid #e4e8ea;
    position: relative;
  }
  .author{
    left: -20px;
    position: absolute;
    top: 0px;
  }
  .author img{
    cursor: pointer;
    border-radius: 50%;
    border: 1px solid #ddd;
    padding: 3px;
    height: 38px;
    width: 38px;
  }
  .sidebar{
    background: linear-gradient(to right, rgba(248,248,248,1) 0%, rgba(255,255,255,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f8f8f8', endColorstr='#ffffff', GradientType=1 );
    border-left: 1px solid #e4e8ea;
    overflow: hidden;
    padding: 60px 0 60px 0px;
    margin: 0px;
  }
  .slide{
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  }
  .widget{
    margin: 0 0 40px;
    padding: 0 0 40px 25px;
    display: block;
    border-bottom: 4px double #e4e8ea;
  }
  .widget h3{
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    display: block;
    margin: 0 0 22px;
  }
  .widget li{
    border-bottom: 1px dotted #b4bfc5;
    padding: 9px 0;
    color: #737f85;
    font-size: 15px;
    line-height: 24px;
    margin: 0;
    vertical-align: top;
    cursor: pointer;
  }
  .widget a{
    color: #737f85;
  }
  .widget a:hover{
    color: #2a7bc1;
  }
</style>



